import VeryAxios from 'very-axios/index.js';
import { Modal } from 'ant-design-vue';

let veryAxiosConfig = {
    tip: true,
    tipFn: (message) => Modal.error({ content: message }),
    cancelDuplicated: true,
    getResStatus: (resData) => {
        if (resData.code) return resData.code;
        if (resData != null) return 200;
        return 0;
    },
    validateStatus: (status) => status >= 200 && status < 300,
    getResErrMsg: (resData) => resData.message,
    getResData: (resData) => {
        if (resData.data) return resData.data;
        if (resData != null) return resData;
        return null;
    },
};

let axiosConfig = {
    baseURL:
        process.env.NODE_ENV == 'development' ? 'http://127.0.0.1:3000/api' : 'https://tools.tuzim.net/api',
    timeout: 12000,
};

const request = new VeryAxios(veryAxiosConfig, axiosConfig);

export default {
    getSettings: () => request.GET('/settings'),
    getDecoder: () => request.GET('/decoder'),
    getWx: (wxid) => request.GET('/decoder/wx', { wx: wxid }),
    getUrlImage: (url) => request.POST('/image/remote', { url }),
    getMarkDown: (id) => request.GET(`/blog/${id}.md`, {}, { baseURL: '' }),
};
